.ForgotPasBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 413px;
    margin: 60px auto 0;
    background: #FFFFFF;
    box-shadow: 1px 1px 10px rgba(187, 187, 187, 0.35);
    border-radius: 10px;
    padding: 50px 50px;
}

.title {
    font-size: 1.8rem;
    font-weight: 600;
    user-select: none;
}

.form {
    width: 100%;
    text-align: center;
}

.input {
    margin-bottom: 10px;
    width: 347px;
}

.error {
    box-sizing: border-box;
    margin: 3px 0 5px 0;
    font-size: 0.85rem;
    color: rgba(208, 0, 0, 0.63);
    height: 20px;
    user-select: none;
}

.dontHaveAccountTitle {
    color: #757575;
    user-select: none;
    margin-top: 30px;
    margin-bottom: 15px;
}

.signForgotPas {
    user-select: none;
    text-decoration: none;
    color: black;
}

.signForgotPas:hover {
    color: #072c57;
}

.login {
    margin-top: 10px;
}

.button{
    border-radius: 30px !important;
    width: 370px;
    height: 36px;
    background-color: #366EFF !important;
    margin-top: 45px !important;
    margin-bottom: 31px !important;
    text-transform: none !important;
    font-size: 16px !important;
    font-weight: 500;
}
